(function () {

    var $window = $(window),
        $document = $(document),
        $body = $('body');

    var progressBar = {
        $barWrapper: $('.progress-bar--wrapper'),
        $bar: $('.progress-bar'),
        scrollPercent: null,
        lock: true,


        init: function () {
            progressBar.controller();
        },
        controller: function () {

            $window.on("scroll load", function () {
                progressBar.scrollPercent = 100 * $window.scrollTop() / ($document.height() - $window.height() - $('footer').outerHeight());
                if ((progressBar.scrollPercent > 100) || ($window.height() >= $('main').height() + $('header').outerHeight()))
                    progressBar.scrollPercent = 100;

                if (progressBar.lock)
                    progressBar.$bar.stop().animate({
                        width: progressBar.scrollPercent + "%"
                    }, 50);

            })


        }
    };

    UX.progressBar = progressBar;

})();