import utils from "./utils";

/**
 * Uploader File Item
 * @param {File|string} file
 * @param {number} index
 * @constructor
 */
function UploaderFile(file, index) {

    /**
     * Unique identifier for this file
     * @type {number}
     */
    this.index = index;

    /**
     * File Object to be uploaded
     * @type {File|string}
     */
    this.file = file;


    /**
     * Current state
     * @type {string}
     */
    this.state = 'uploading';

    /**
     *
     * @type {XMLHttpRequest|null}
     */
    this.request = null;
}

UploaderFile.prototype = {

    constructor: UploaderFile,

    /**
     * Get index of the file
     * @returns {number}
     */
    getIndex: function() {
        return this.index;
    },

    /**
     * Get the file object itself
     * @returns {File}
     */
    getFile: function() {
        return this.file;
    },

    /**
     * get the file size
     * @returns {number}
     */
    fileSize: function () {
        return this.file.size;
    },

    /**
     * get file size in human readable format
     * @returns {*}
     */
    fileSizeHuman: function () {
        return utils.methods.fileSizeHuman(this.file.size);
    },

    /**
     * get the file name
     * @returns {string}
     */
    fileName: function () {
        if (typeof this.file === "string")
            return this.file;
        return this.file.name;
    },

    /**
     *
     * @param {int} [numberOfChar]
     * @returns {string}
     */
    fileNameTruncated: function (numberOfChar) {
        return utils.methods.truncate(this.file.name, numberOfChar || 53);
    },

    /**
     * Get the extension of the file
     * @returns {string}
     */
    fileExtension: function() {
        return utils.methods.fileExtension(this.file.name)
    },

    getType() {
        return this.file.type;
    },

    /**
     * Set upload AJAX request
     * @param {XMLHttpRequest} xhr
     */
    setRequest: function (xhr) {
        this.request = xhr;
    },

    /**
     * Get upload AJAX request
     * @returns {XMLHttpRequest}
     */
    getRequest: function () {
        return this.request;
    },

    /**
     * Stop uploading operation, cancels the HTTP request
     */
    abort: function () {
        if (this.request)
            this.request.abort()
    },

    /**
     * Update the current state of the uploading file
     * @param {string} newState
     */
    setState: function (newState) {
        this.state = newState;
    },

    /**
     * Get the current state of the uploading file
     * @returns {string}
     */
    getState: function () {
        return this.state;
    }
};


export default UploaderFile;