UX.basicMetadata.autocomplete.sources.journals = function (url, request, response) {
    $.ajax({
        url: 'https://jsonplaceholder.typicode.com/posts',
        dataType: 'json',
        data: {
            query: request.term
        },
        success: function (data) {
            response($.map(data, function (el) {
                return {
                    label: el.title,
                    value: el.title,
                    id: el.id
                };
            }));
        }
    });
};